<template>
  <CardGrid v-if="!props.contentLoading">
    <ReleaseCard
      v-for="card in props.items"
      :key="card.uid"
      v-bind="card"
      :installDisabled="isInstallationInProgress"
      @installRelease="(uid) => emit('installRelease', uid)"
      @showChangelog="(uid) => emit('showChangelog', uid)"
    />
  </CardGrid>

  <CardBlock v-else>
    <template #content>
      <PlaceholderContent />
    </template>
  </CardBlock>
</template>

<script setup lang="ts">
import ReleaseCard, { type ReleaseCardInterface } from "@/pages/releases/list/components/Card.vue";
import CardGrid from "@/shared/ui_kit/card/CardList.vue";
import { array, bool } from "vue-types";
import CardBlock from "@/shared/ui_kit/card/CardBlock.vue";
import PlaceholderContent from "@/shared/ui_kit/PlaceholderContent.vue";
import { computed } from "vue";

const props = defineProps({
  items: array<ReleaseCardInterface>().def(() => []),
  contentLoading: bool().def(false),
});

const emit = defineEmits<{
  (e: "installRelease", uid: ReleaseCardInterface["uid"]): void;
  (e: "showChangelog", uid: ReleaseCardInterface["uid"]): void;
}>();

const isInstallationInProgress = computed(() => props.items.some((item) => item.status === "pending"));
</script>
